.products {
    padding: 50px 20px;
    background-color: #F2E1C1; /* Fondo beige */
    text-align: center;
  }
  
  .products h2 {
    font-size: 2.5em;
    margin-bottom: 40px;
    color: #3C2A3A; /* Texto marrón oscuro */
  }
  
  .product-list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .product-item {
    width: 250px;
    margin: 20px;
    padding: 20px;
    border: 1px solid #CCC;
    border-radius: 10px;
    background-color: #FFF;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .product-item img {
    width: 100%;
    border-bottom: 1px solid #CCC;
    margin-bottom: 15px;
  }
  
  .product-item p {
    color: #3C2A3A; /* Texto marrón oscuro */
  }
  