header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #5C3A4B; /* Fondo marrón oscuro */
  }
  
  header img {
    height: 50px;
  }
  
  nav ul {
    display: flex;
    list-style: none;
  }
  
  nav ul li {
    margin: 0 15px;
  }
  
  nav ul li a {
    text-decoration: none;
    color: #FFF;
    font-weight: bold;
  }
  
  nav ul li a:hover {
    color: #FFD700; /* Amarillo dorado */
  }
  